@import "../../node_modules/@cupcake-ds/cupcake/src/scss/support/support.scss";

// Load docs components  
@import "variables";
@import "header";
@import "landing";
@import "article";
@import "content";
@import "sidebar";
@import "tabs";
@import "component-examples";
@import "callouts";

// Load docs dependencies
@import "syntax";
@import "anchor";
@import "algolia";


@media (max-width:767px) {
  .doc-sidenav {
    width: 100%;
    position: relative;
  }

  .doc-sidenav-body {
    display: none;
  }

  .main-container {
    width: 100%;
    margin-left: 0 !important;
  }

  .docs-bg-img {
    padding: 1.5rem 1rem !important;
  }
}

[data-tooltip]::after {
  padding: 4px 10px;
  border-radius: 3px;
  line-height: 12px;
  max-width: 300px;
  font-size: 12px;
}

iframe {
  border: 0;
}


::-webkit-scrollbar {
  width: .25em;
}

::-webkit-scrollbar-track {
  background-color: #f8f9fa;
}

::-webkit-scrollbar-thumb {
  background-color: #ced4da;
  outline: none;
}


.docs-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

